import { borderSize, cellSize } from '../config'
import { convertLocalPositionToGlobal, getIndexByPosition } from '../utils'
import { Teleport } from '../models/objects'
import { renderText } from './shapes'
import { assets } from './loader'

export function renderTeleports({
  context,
  teleports,
  indexesVisible = false,
}: {
  context: CanvasRenderingContext2D
  teleports: Teleport[]
  indexesVisible?: boolean
}): void {
  teleports.forEach((teleport) => {
    const position = teleport.current
    const [x, y] = convertLocalPositionToGlobal(position)

    const size = cellSize - borderSize * 2

    context.drawImage(
      assets.hole,
      x + borderSize * 2,
      y + borderSize * 2,
      size,
      size
    )

    if (indexesVisible) {
      renderText({
        context,
        text: getIndexByPosition(position).toString(),
        position,
      })
    }
  })
}
