import { createStore } from 'effector'
import { generateRandomFoodByCount, generateRandomTeleports } from '../../utils'
import { foodCount, teleportCount } from '../../config'

export type Teleport = {
  current: Coords
  target: Coords
}

export const $foods = createStore<Food[]>(generateRandomFoodByCount(foodCount))

export const $teleports = createStore<Teleport[]>(
  generateRandomTeleports(teleportCount)
)

export const $bricks = createStore<Coords[]>([])
